*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 50px;
}
input{
  font-size: 0.9rem;
  width: 300px;
  color: gray;
}

.card-grid{
display: flex;
flex-direction: row;
flex-wrap: wrap;
}
.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}
.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}
